<template>
	<view class="my">
		<view class="myInfo" :style="{backgroundImage:`url(${myBgImg})`}" @tap="goMyInfo">
			<view class="infoLeft">
				<image :src="userInfo.headSculpture ? userInfo.headSculpture : headerImg" mode="aspectFill"></image>
				<text>{{userInfo.username}}</text>
			</view>
			<view class="editBox">
				<image class="set" src="@/static/moreIcon/more_bianji.png" mode=""></image>
				<text>编辑</text>
			</view>
		</view>
		<view class="listBox">
			<!-- 视角切换 -->
			<view class="list" @click="switchTap">
				<view>
					<image src="/static/moreIcon/icon3.png" mode=""></image>
					<text>视角切换</text>
				</view>
				<uni-icons class="icon" type="arrowright" size="14" color="#999"></uni-icons>
			</view>
			<template v-if="role == 3">
				<view class="list" v-for="(item,index) in myList" :key="index" @click="listTap(index)">
					<view>
						<image :src="item.img" mode=""></image>
						<text>{{item.name}}</text>
					</view>
					<uni-icons class="icon" type="arrowright" size="14" color="#999"></uni-icons>
				</view>
			</template>
			<template v-else-if="role == 4">
				<view class="list" v-for="(item,index) in myList2" :key="index" @click="listTap2(index)">
					<view>
						<image :src="item.img" mode=""></image>
						<text>{{item.name}}</text>
					</view>
					<uni-icons class="icon" type="arrowright" size="14" color="#999"></uni-icons>
				</view>
			</template>
			<template v-else>
				<view class="list" v-for="(item,index) in myList3" :key="index" @click="listTap3(index)">
					<view>
						<image :src="item.img" mode=""></image>
						<text>{{item.name}}</text>
					</view>
					<uni-icons class="icon" type="arrowright" size="14" color="#999"></uni-icons>
				</view>
			</template>
		</view>
		<view class="signOut" @click="loginOut">退出登录</view>
		<switchPoup ref="switchPoup"></switchPoup>
		<projectPoup ref="projectPoup"></projectPoup>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import header from '@/static/moreIcon/avatar.png';
	import myBg from '@/static/moreIcon/myBg.png';
	import switchPoup from "@/components/switchPoup.vue"
	import projectPoup from "@/pages/salemanVisual/components/projectPoup.vue"
	export default {
		props: ['role', 'userInfo'],
		data() {
			return {
				myList: [{
					name: '项目切换',
					img: '/static/moreIcon/icon8.png'
				}, {
					name: '业绩',
					img: '/static/moreIcon/icon5.png'
				}],
				myList2: [{
					name: '项目切换',
					img: '/static/moreIcon/icon8.png'
				}, {
					name: '我的审批',
					img: '/static/moreIcon/icon2.png'
				}],
				myList3: [{
					name: '我的审批',
					img: '/static/moreIcon/icon2.png'
				}, {
					name: '来访分析',
					img: '/static/moreIcon/icon6.png'
				}, {
					name: '转化率',
					img: '/static/moreIcon/icon4.png'
				}, {
					name: '逾期情况',
					img: '/static/moreIcon/icon7.png'
				}],
				headerImg: header,
				myBgImg: myBg
			}
		},
		methods: {
			// 视角切换
			switchTap(){
				this.$refs.switchPoup.open()
			},
			// 销售人员视角
			listTap(index) {
				switch (index) {
					case 0:
						this.$refs.projectPoup.open()
						break
					case 1:
						uni.navigateTo({
							url: '/pages/salemanVisual/more/achievement'
						})
						break
				}
			},
			// 销售经理视角
			listTap2(index) {
				switch (index) {
					case 0:
						this.$refs.projectPoup.open(1)
						break
					case 1:
						uni.navigateTo({
							url: '/pages/chiefVisual/more/approval/approval'
						})
						break
				}
			},
			// 销售总监视角
			listTap3(index) {
				switch (index) {
					case 0:
						uni.navigateTo({
							url: '/pages/chiefVisual/more/approval/approval'
						})
						break
					case 1:
						uni.navigateTo({
							url: '/pages/chiefVisual/more/callAny/callAny'
						})
						break
					case 2:
						uni.navigateTo({
							url: '/pages/chiefVisual/more/transformRate/transformRate'
						})
						break
					case 3:
						uni.navigateTo({
							url: '/pages/chiefVisual/more/overdue/overdue'
						})
						break
				}
			},
			// 个人主页
			goMyInfo() {
				uni.navigateTo({
					url: '/pages/salemanVisual/more/set'
				})
			},
			// 退出登录
			loginOut(){
				uni.showModal({
					title: '提示',
					content: '确定退出登录吗？',
					success: function (res) {
						if (res.confirm) {
							Tools.showLoading('退出中')
							httpUtils.sendHttpRequest('1004', Object.assign({}, ''), function(res) {
								Tools.hideLoading()
								if (res.data.code == '0') {
									Tools.showSuccessToast('退出成功')
									let loginInfo = uni.getStorageSync('loginInfo')
									loginInfo = {};
									uni.setStorageSync('loginInfo', loginInfo);
									setTimeout(function(){
										uni.reLaunch({
											url: '/pages/login',
										})
									},1000)
								}
							})
						}
					}
				});
			},
		},
		components: {
			switchPoup,
			projectPoup
		}
	}
</script>

<style lang="scss">
	@import '@/styles/my.scss';
	page{
		height: 100%;
	}
	.my{
		height: 100%;
		position: relative;
	}
</style>